/* eslint-disable react/no-danger */
/* eslint-disable react/prop-types */
/* eslint-disable react/jsx-props-no-spreading */
import { Icon } from 'components/atoms/icon/icon';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from 'components/molecues/accordion/accordion';
import { ThemeSvgGradient } from 'components/molecues/theme-svg-gradient/theme-svg-gradient';
import { graphql, useStaticQuery } from 'gatsby';
import React from 'react';

export const FaqPageContent = () => {
  const {
    allMarkdownRemark: { nodes: questions },
  } = useStaticQuery<Queries.AllFaqQuestionsQuery>(graphql`
  query AllFaqQuestions {
   allMarkdownRemark(
    filter: {
     fileAbsolutePath: {}
     frontmatter: { type: { eq: "faq-question" } }
    }
   ) {
    nodes {
     frontmatter {
      question
      title
     }
     html
     id
    }
   }
  }
 `);

  return (
    <div className="flex w-full flex-col items-center justify-start gap-12 py-36">
      <h1 className="font-uni-sans text-3xl">FAQ</h1>
      <Accordion type="single" collapsible className="lg:max-w-3xl">
        {questions.map((question) => (
          <AccordionItem
            key={question.id}
            value={`${question.id}-${question.frontmatter?.question}`}
            className="group"
          >
            <AccordionTrigger>
              <div className="flex h-20 items-center justify-between">
                {question.frontmatter?.question}
                <Icon
                  name="ChevronDown"
                  className="h-2 fill-[url(#chevron-down-button)] transition-all group-data-[state=open]:rotate-180"
                >
                  <ThemeSvgGradient id="chevron-down-button" />
                </Icon>
              </div>
            </AccordionTrigger>
            <AccordionContent>
              <section
                className="prose max-w-none px-8 py-4 text-main-white"
                dangerouslySetInnerHTML={{ __html: question.html! }}
              />
            </AccordionContent>
          </AccordionItem>
        ))}
      </Accordion>
    </div>
  );
};
