/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import * as RadixAccordion from '@radix-ui/react-accordion';

type AccordionItemProps = RadixAccordion.AccordionItemProps;

export const AccordionItem = ({ className, ...restProps }: AccordionItemProps) => (
  <RadixAccordion.Item {...restProps} className={`${className}`} />
);

type AccordionTriggerProps = RadixAccordion.AccordionTriggerProps;

export const AccordionTrigger = React.forwardRef<
HTMLButtonElement,
AccordionTriggerProps
>(
  ({ className, ...restProps }: AccordionTriggerProps, ref) => (
    <RadixAccordion.Trigger {...restProps} ref={ref} className={`${className} w-full rounded-2xl bg-background-light px-9 text-start font-uni-sans text-lg`} />
  ),
);

type AccordionHeaderProps = RadixAccordion.AccordionHeaderProps;

export const AccordionHeader = (props: AccordionHeaderProps) => (
  <RadixAccordion.Header {...props} />
);

type AccordionContentProps = RadixAccordion.AccordionContentProps;

export const AccordionContent = React.forwardRef<
  HTMLDivElement,
  AccordionContentProps
>(({ className, ...restProps }: AccordionContentProps, ref) => (
  <RadixAccordion.Content {...restProps} className={`${className} overflow-hidden group-data-[state=open]:animate-slideDown group-data-[state=closed]:animate-slideUp`} ref={ref} />
));

type AccordionProps =
  | RadixAccordion.AccordionSingleProps
  | (RadixAccordion.AccordionMultipleProps & {
    children: React.ReactNode | React.ReactNode[];
  });

export const Accordion = ({ children, className, ...restProps }: AccordionProps) => (
  <RadixAccordion.Root {...restProps} className={`${className} flex w-full flex-col gap-6`}>{children}</RadixAccordion.Root>
);
